'use strict';

// MODULES //

import numelDimension from '@stdlib/ndarray-base-numel-dimension';
import getStride from '@stdlib/ndarray-base-stride';
import getOffset from '@stdlib/ndarray-base-offset';
import getData from '@stdlib/ndarray-base-data-buffer';
import ndarraylike2scalar from '@stdlib/ndarray-base-ndarraylike2scalar';
import { ndarray as strided } from '@stdlib/blas-ext-base-scusumkbn';


// MAIN //

/**
* Computes the cumulative sum of a one-dimensional single-precision floating-point ndarray using an improved Kahan–Babuška algorithm.
*
* @param {ArrayLikeObject<Object>} arrays - array-like object containing an input ndarray, an output ndarray, and an ndarray containing the initial sum
* @returns {Object} output ndarray
*
* @example
* import Float32Array from '@stdlib/array-float32';
* import ndarray2array from '@stdlib/ndarray-to-array';
* import ndarray from '@stdlib/ndarray-base-ctor';
* import scalar2ndarray from '@stdlib/ndarray-base-from-scalar';
*
* var xbuf = new Float32Array( [ 1.0, 3.0, 4.0, 2.0 ] );
* var x = new ndarray( 'float32', xbuf, [ 4 ], [ 1 ], 0, 'row-major' );
*
* var ybuf = new Float32Array( [ 0.0, 0.0, 0.0, 0.0 ] );
* var y = new ndarray( 'float32', ybuf, [ 4 ], [ 1 ], 0, 'row-major' );
*
* var initial = scalar2ndarray( 0.0, 'float32', 'row-major' );
*
* var v = scusumkbn( [ x, y, initial ] );
* // returns <ndarray>
*
* var bool = ( v === y );
* // returns true
*
* var arr = ndarray2array( v );
* // returns [ 1.0, 4.0, 8.0, 10.0 ]
*/
function scusumkbn( arrays ) {
	var x = arrays[ 0 ];
	var y = arrays[ 1 ];
	var v = ndarraylike2scalar( arrays[ 2 ] );
	strided( numelDimension( x, 0 ), v, getData( x ), getStride( x, 0 ), getOffset( x ), getData( y ), getStride( y, 0 ), getOffset( y ) ); // eslint-disable-line max-len
	return y;
}


// EXPORTS //

export default scusumkbn;
